// Create or define namespace
bizagi = typeof bizagi !== "undefined" ? bizagi : {};
bizagi.services = typeof bizagi.services !== "undefined" ? bizagi.services : {};
bizagi.services.ajax = typeof bizagi.services.ajax !== "undefined" ? bizagi.services.ajax : {};
bizagi.log = bizagi.log || function () {};
bizagi.assert = bizagi.assert || function () {};
// Global variables
bizagi.services.ajax.useAbsolutePath = typeof BIZAGI_USE_ABSOLUTE_PATH !== "undefined" ? BIZAGI_USE_ABSOLUTE_PATH : false;
bizagi.services.ajax.pathToBase = bizagi.services.ajax.useAbsolutePath ? bizagi.loader.getLocationPrefix() : typeof BIZAGI_PATH_TO_BASE !== "undefined" ? BIZAGI_PATH_TO_BASE : "";
bizagi.services.ajax.loginPage = bizagi.services.ajax.pathToBase || "default.htm";
bizagi.services.ajax.errorPage = bizagi.services.ajax.pathToBase + "error.html";
bizagi.services.ajax.rpcEnabled = false;
bizagi.services.ajax.rpcHandler = "RestServices/JsonRpcHandler.ashx";
bizagi.services.ajax.logEnabled = true;
bizagi.services.ajax.logSuccess = true;
bizagi.services.ajax.logSubmits = true;
bizagi.services.ajax.logFail = true;
bizagi.services.ajax.batch = {};
bizagi.services.ajax.batchCount = 0;
bizagi.services.ajax.batchTimer = 100;
bizagi.services.ajax.batchTimeoutInstance = null;

bizagi.services.ajax.parseJSON = function (data) {
  var result = {};
  // Remove break line
  data = data.replace(/(\r\n|\n|\r)/gm, "");

  try {
    result = $.parseJSON(data);
  } catch (e) {
    try {
      // Try to fix some known problems regarding rare ascii characters
      result = $.parseJSON(data.replace(/\x1d/g, "."));
    } catch (ex) {
      result.type = "error";
      result.code = e.errorCode;
      result.message = data;
      //Throw exception in next layer
    }
  }

  if (result.type && result.type == "error" || result.status && result.status == "error") {
    if (result.code == "AUTH_ERROR" || result.code == "AUTHENTICATION_ERROR") {
      // Redirect to login page
      /*if (typeof bizagi.services.ajax.loginPage === "string") {
       // Remove session storage of authentication data
       sessionStorage.removeItem("bizagiAuthentication");
       if (localStorage.getItem("clientLog") != "true") {
       // Reload page
       //window.location = bizagi.services.ajax.loginPage;
       console.warn("Debe autenticarse");
       }
       } else {
       bizagi.services.ajax.loginPage();
       }*/

    } else if (result.code == "FED_AUTHENTICATION_ERROR") {
      /*if (typeof bizagi.services.ajax.loginPage === "string") {
                                                            sessionStorage.setItem("FED_AUTHENTICATION_ERROR", true);
                                                            window.location = result.message;
                                                            } else {
                                                            bizagi.services.ajax.loginPage();
                                                            }*/
    }

    if (result.code == "licenseError") {
      window.location = bizagi.services.ajax.errorPage + "?message=" + result.message + "&type=" + result.type;
    }

    var message = (result.code ? result.code : "") + (result.message ? "" + result.message : "");
    jQuery.error(message);
  }
  localStorage.setItem("clientLog", "false");
  return result;
};

$.ajaxSetup({
  converters: {
    "text json": bizagi.services.ajax.parseJSON },

  beforeSend: function beforeSend(jqXHR, settings) {
    if (settings.dataType == "json" && !settings.batchRequest) {
      jqXHR._loading = true;
      bizagi && bizagi.loading && bizagi.loading.start();

    }
  },
  complete: function complete(jqXHR) {
    if (jqXHR._loading) {
      bizagi && bizagi.loading && bizagi.loading.stop();
    }
    if (jqXHR.status === 401) {
      if (jqXHR.responseJSON && jqXHR.responseJSON.validationMethod === "implicitPrincipalInjection") {
        return;
      }

      bizagi.services.redirectToLoginPage();
    } else if (jqXHR.status === 503 && typeof cordova === "undefined") {
      bizagi.loader.startPageMaintenance();
    }
  } });

var jqAutoLogin = function jqAutoLogin(options) {
  this.options = {};
  this.user = {};
  this.intercept = false;
  this.refreshing = false;
  this.buffer = [];
  this.currentRequests = [];

  this._resetOptions();
  this._setupInterceptor();

  $.extend(this.options, options);

  this._resetData();
};

// Public methods

jqAutoLogin.prototype.logout = function logout() {
  this._resetData();
  this._deactivateInterceptor();
  this._fireEvent("logout");
};

jqAutoLogin.prototype.login = function login(user) {
  this.setUserData(user);
  this._activateInterceptor();
  this._fireEvent("login");
};

jqAutoLogin.prototype.setUserData = function setUserData(user) {
  this.user = {
    user: user.user || null,
    password: user.password || null,
    domain: user.domain || null };

};

// Private methods

jqAutoLogin.prototype._activateInterceptor = function _activateInterceptor() {
  this.intercept = true;
};

jqAutoLogin.prototype._addToBuffer = function _addToBuffer(settings, deferred) {
  this.buffer.push({
    deferred: deferred,
    settings: settings });

};

jqAutoLogin.prototype._clearBuffer = function _clearBuffer() {
  this.buffer = [];
};

jqAutoLogin.prototype._deactivateInterceptor = function _deactivateInterceptor() {
  this.intercept = false;
};

jqAutoLogin.prototype._fireBuffer = function _fireBuffer() {
  var self = this;
  var deferred;
  var promises = [];

  for (var i in this.buffer) {
    deferred = this.buffer[i].deferred;

    this.buffer[i].settings.refreshRetry = true;

    promises.push($.ajax(this.buffer[i].settings).then(deferred.resolve, deferred.reject));
  }

  this._clearBuffer();

  $.when.apply($, promises).
  done(function () {
    self._setRefreshingFlag(false);
  }).
  fail(function () {
    self._setRefreshingFlag(false);
    self.logout();
  });
};

jqAutoLogin.prototype._fireEvent = function _fireEvent(eventType) {
  if (this._hasEvent(eventType)) {
    return this.options.events[eventType]();
  }
};

jqAutoLogin.prototype._hasEvent = function _hasEvent(eventType) {
  return this.options.events[eventType] !== undefined && typeof this.options.events[eventType] === "function";
};

jqAutoLogin.prototype._resetData = function _resetData() {
  this.user = {
    user: null,
    password: null,
    domain: null };

};

jqAutoLogin.prototype._resetOptions = function _resetOptions() {
  this.options = {
    bufferInterval: 25,
    bufferWaitLimit: 500,
    events: {} };

};

jqAutoLogin.prototype._setRefreshingFlag = function _setRefreshingFlag(newFlag) {
  this.refreshing = newFlag;
};

jqAutoLogin.prototype._setupInterceptor = function _setupInterceptor() {
  var self = this;

  // Credits to gnarf @ http://stackoverflow.com/a/12446363/602488
  $.ajaxPrefilter(function (options, originalOptions, jqxhr) {
    if (options.refreshRetry === true) {
      return;
    }

    var deferred = $.Deferred();

    self.currentRequests.push(options.url);
    jqxhr.always(function () {
      self.currentRequests.splice($.inArray(options.url, self.currentRequests), 1);
    });
    jqxhr.done(deferred.resolve);
    jqxhr.fail(function () {
      var args = Array.prototype.slice.call(arguments);

      if (self.intercept && jqxhr.status === 401 && self._hasEvent("sessionExpiration")) {
        self._addToBuffer(options, deferred);

        if (!self.refreshing) {
          self._setRefreshingFlag(true);
          self._fireEvent("sessionExpiration").
          done(function () {
            // Setup buffer interval that waits for all sent requests to return
            var waited = 0;
            self.interval = setInterval(function () {
              waited += self.options.bufferInterval;

              // All requests have returned 401 and have been buffered
              if (self.currentRequests.length === 0 || waited >= self.options.bufferWaitLimit) {
                clearInterval(self.interval);
                self._fireBuffer();
              }
            }, self.options.bufferInterval);
          }).
          fail(function () {
            self.logout();
          });
        }
      } else {
        deferred.rejectWith(jqxhr, args);
      }
    });

    return deferred.promise(jqxhr);
  });
};