/*
*   Name: BizAgi Device Render Factory
*   Author: Ricardo Prez
*   Comments:
*   -   This script will detect the current device and then will load the appropiate factory on demand
*/

$.Class.extend("bizagi.mashup.device.factory", {}, {

  /**
                                                      * Constructor
                                                      * @param mashup
                                                      */
  init: function init(mashup, params) {
    this.device = params.device || bizagi.detectDevice();
    this.mashup = mashup;
    this.cachedFacade = null;
  },


  /**
      * Returns the appropiate mashup facade based on the device detection
      * Because it loads the scripts on demand it must return a promise
      * @param dataService
      * @param initializeTemplates
      * @returns {*}
      */
  getMashupFacade: function getMashupFacade(dataService, initializeTemplates) {
    var self = this;
    var defer = new $.Deferred();
    initializeTemplates = typeof initializeTemplates !== "undefined" ? initializeTemplates : true;

    if (self.cachedFacade != null) {
      // Resolve with cached factory                
      defer.resolve(self.cachedFacade);

    } else {
      if (bizagi.util.isTabletDevice()) {

        self.cachedFacade = new bizagi.mashup.tablet.facade(this.mashup, dataService);

        // Chain template loading
        $.when(self.cachedFacade.initAsyncStuff({
          device: this.device })).

        done(function () {
          // Resolve operation
          defer.resolve(self.cachedFacade);
        });

        return defer.promise();
      } else if (bizagi.util.isSmartphoneDevice()) {

        self.cachedFacade = new bizagi.mashup.smartphone.facade(this.mashup, dataService);

        // Chain template loading
        $.when(self.cachedFacade.initAsyncStuff({
          device: this.device })).

        done(function () {
          // Resolve operation
          defer.resolve(self.cachedFacade);
        });

        return defer.promise();
      } else {
        // If no device suuported
        alert("Not supported device: " + this.device);
        defer.resolve("");
      }
    }

    return defer.promise();
  },

  /*
     *   Returns the current data service being used
     */
  getDataService: function getDataService() {
    return this.dataService;
  } });