/*
 *   Name: BizAgi Workportal Main Controller
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will define a base class to handle workportal layouts for any device
 */

$.Class.extend("bizagi.mashup.controllers.controller", {}, {

  /**
                                                              *   Constructor
                                                              */
  init: function init(mashupFacade, dataService, params) {
    // Set mashup facade
    this.mashupFacade = mashupFacade;

    // Set data service
    this.dataService = dataService;

    // Set l10n service
    this.resources = bizagi.localization;

    // Set a component container hash to use with templates
    this.componentContainers = {};

    // Define an object to hold the templates used in the widget
    this.tmpl = {};

    // Define an object to hold the subscribe collection
    this.subscribers = [];

    // Flag to check if the element is disposed
    this.disposed = false;

    this.creationParams = params || {};

    this.canvas = this.creationParams.canvas || $("<div />");
  },

  /**
      *
      * @param id
      * @param transition
      */
  navigate: function navigate(id, transition, replace) {
    var self = this;
    self.mashupFacade.navigate(id, transition, replace);
  },

  /**
      *   Publish an event so any controller can subscribe to it
      */
  publish: function publish(eventName, params) {
    if (this.disposed) return null;
    return $(document).triggerHandler(eventName, params);
  },

  /**
      *   Subscribe to an event that any controller could trigger
      */
  subscribe: function subscribe(eventName, callback) {
    if (this.disposed) return null;

    var docEvents = jQuery._data(document, "events");
    var docEventName = docEvents[eventName];
    if (docEventName) {
      var length = docEventName.length;

      for (var i = 0, len = length; i < len; i++) {
        if (docEventName[i].handler.toString() === callback.toString()) {
          return;
        }
      }
    }

    $(document).bind(eventName, callback);
    this.subscribers.push({ event: eventName });
  },

  /**
      *   Subscribe to one time event that any controller could trigger
      */
  subscribeOneTime: function subscribeOneTime(eventName, callback) {
    if (this.disposed) return null;
    $(document).one(eventName, callback);
    this.subscribers.push({ event: eventName });
  },

  /**
      *   Un Subscribe to an event that any controller could trigger in this document
      */
  unsubscribe: function unsubscribe(eventName, callback) {
    if (this.disposed) return null;
    if (callback) {
      $(document).unbind(eventName, callback);
    } else
    {$(document).unbind(eventName);}
  },

  /**
      *   Makes the base processing of the layout (mix templates + data)
      *   after all that processing will call a post-render method to
      *   be implemented in each device to apply custom plugins
      *   Returns a promise because the rendering could be asynchronous
      */
  render: function render(params) {
    var self = this;
    var defer = new $.Deferred();

    if (typeof self.templatesDeferred !== "undefined") {
      self.templatesDeferred.then(function () {
        // Render content
        self.internalRender(params).done(function (content) {
          defer.resolve(content);
        });
      });
    } else
    {
      self.internalRender(params).done(function (content) {
        defer.resolve(content);
      });
    }

    return defer.promise();
  },

  /**
      * to implement in each device
      * @param params
      */
  configureHandlers: function configureHandlers(params) {},

  /**
                                                             *
                                                             * @returns {*}
                                                             */
  internalRender: function internalRender(params) {
    var self = this;
    return $.when(self.renderContent(params)).
    then(function (content) {
      self.content = content || "";

      self.canvas.append(content);
      // Call post-render method
      self.postRender();
      // Resolve deferred
      return self.content;
    });
  },


  /**
      * Allows load templates
      * @param templates
      * @returns promise of load templates excecutions
      */
  loadTemplates: function loadTemplates(templates) {
    var self = this,
    def = self.templatesDeferred = new $.Deferred(),
    tmplPromises = [];

    var useNewEngine = templates.useNewEngine != undefined ? templates.useNewEngine : true;

    for (var name in templates) {
      if (name != "useNewEngine") {
        tmplPromises.push(
        self.loadTemplate({
          "name": name,
          "path": templates[name],
          "useNewEngine": useNewEngine }));


      }
    }

    $.when.apply($, tmplPromises).
    done(function () {
      def.resolve();
    });

    return def.promise();
  },


  /*
      * Loads template
      */
  loadTemplate: function loadTemplate(data) {
    var self = this;

    if (self.tmpl[data.name]) {
      return self.tmpl[data.name];
    } else {
      if (data.useNewEngine) {
        return bizagi.templateService.getTemplateWidget(data.path, data.name).
        done(function (tmpl) {
          self.tmpl[data.name] = tmpl;
        });
      }

      return self.templateService.getTemplate(data.path, data.name).
      done(function (tmpl) {
        self.tmpl[data.name] = tmpl;
      });

    }
  },

  /*
      * Gets template
      */
  getTemplate: function getTemplate(name) {
    if (this.tmpl.hasOwnProperty(name)) {
      return this.tmpl[name];
    }
    return null;

  },

  /*
      *   Returs the rendered content
      */
  getContent: function getContent() {
    return this.content;
  },

  /*
      *   Returns the mapped resource
      */
  getResource: function getResource(key) {
    return this.resources.getResource(key);
  },

  /*
      *   Renders the content for the current controller
      *
      */
  renderContent: function renderContent() {
    // Override on implementations
    return "";
  },

  /*
      *   To be overriden in each device to apply layouts
      */
  postRender: function postRender() {},

  /*
                                         *   Returns the component replaced tag selector
                                         */
  getComponentSelector: function getComponentSelector(component) {
    return "[data-bizagi-component=" + component + "]";
  },

  /*
      *   Returns the component replaced tag selector
      */
  getComponentContainer: function getComponentContainer(component) {
    var self = this;
    var content = self.getContent();
    if (!content || content.length == 0) return null;

    // Find component parent and cache it
    var componentElement = self.componentContainers[component];
    if (componentElement == null) {
      // Find the component inside the main content
      componentElement = $(self.getComponentSelector(component), content);

      // If still not found, try to check if content element is the component
      if (componentElement.length == 0 && content.is(self.getComponentSelector(component))) {
        componentElement = content;
      }

      // Cache the component
      self.componentContainers[component] = componentElement;
    }
    return self.componentContainers[component];
  },

  /*
      *   Executes a mashup action
      */
  executeAction: function executeAction(actionName, params) {
    var self = this;
    params = params || {};
    var actionParams = $.extend(params, {
      action: actionName });


    var actionController = self.mashupFacade.getAction(actionParams.action);

    // Executes the action
    actionController.execute(actionParams);
  },

  /*
      *   Asks for a full resize
      */
  resizeLayout: function resizeLayout() {
    this.publish("resizeLayout");
  },

  /*
      * Do stuff when a resize has been triggered
      */
  performResizeLayout: function performResizeLayout() {},

  resizeParent: function resizeParent(formElement) {
    var self = this;
    this.sendDimensionsToParent(formElement);
    var canvasobject = formElement[0];
    if (canvasobject.attachEvent) {
      canvasobject.onresize = function () {
        self.sendDimensionsToParent(formElement);
      };
    } else {
      bizagi.addResizeListener(canvasobject, function (e) {
        self.sendDimensionsToParent(formElement);
      });
    }
  },

  sendDimensionsToParent: function sendDimensionsToParent(formElement) {
    this.publish("mashup-resize-content", { height: formElement.outerHeight() });
  },

  /*
      *   Disposes the widget
      */
  dispose: function dispose() {
    var self = this;
    self.disposed = true;

    setTimeout(function () {
      if (self.subscribers != null) {
        // Remove all subcriptions
        $.each(self.subscribers, function (i, item) {
          self.unsubscribe(item.event);
        });
      }
      bizagi.util.dispose(self);
    }, bizagi.override.disposeTime || 50);
  } });