/*
 *   Name: BizAgi Workportal entity Templates Controller
 *   Author: RicardoPD on 2/16/2017.
 *   Comments:
 *      - this script will paint the list of elments in an entity list - with the render - Template component.
 */

bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.entityTemplates", {}, {
  /**
                                                                                                * Constructor
                                                                                                * @param mashupFacade
                                                                                                * @param dataService
                                                                                                * @param params
                                                                                                */
  init: function init(mashupFacade, dataService, params) {
    var self = this;
    self._super(mashupFacade, dataService, params);
    self.acumContext = [];

    if (bizagi.hasOwnProperty("kendo")) {
      this.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
    } else {
      this.templateService = new bizagi.templates.services.service(bizagi.localization);
    }
  },
  /**
      * This controller haven''t a visual interface
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("mashup-entity-templates");
    return $.kendoTmpls(template, {});
  },

  postRender: function postRender() {
    var self = this;
    self.view = new kendo.mobile.ui.View(self.content, {});
    self.navbar = $(".km-navbar", self.view.header).data("kendoMobileNavBar");

    self.entityDataSource = new kendo.data.DataSource({
      transport: {
        read: function read(options) {
          bizagi.loading.start();
          // make JSONP request to http://demos.telerik.com/kendo-ui/service/products
          var listParams = $.extend({
            page: options.data.page || 1,
            pageSize: options.data.pageSize || 40 },
          options.data.filter.filters[0].value);

          $.when(self.getEntities(listParams)).then(function (result) {
            self.dataCalled(result);
            options.success(result);
          }).fail(function (error) {
            options.error(error);
          }).always(function () {
            bizagi.loading.stop();
          });
        } },

      schema: {
        data: "entities",
        total: "totalRecords" },

      serverFiltering: true,
      serverPaging: true,
      page: 1,
      pageSize: self.Class.pageSize });


    var templateLoaderPush = self.getTemplate("mashup-entity-templates-pull-loader");
    var templateLoaderRefresh = self.getTemplate("mashup-entity-templates-refresh-loader");

    self.entityList = self.content.find(".entity-list").kendoMobileCustomListView({
      autoBind: false,
      loadMore: true,
      messages: {
        pullTemplate: templateLoaderPush,
        refreshTemplate: templateLoaderRefresh,
        releaseTemplate: templateLoaderRefresh,
        loadMoreText: self.getResource("workportal-widget-inbox-load-more") },

      dataSource: self.entityDataSource,
      template: self.getTemplate("mashup-entity-templates-item"),
      pullToRefresh: true,
      itemBind: function itemBind(item) {
        $(".wp-tender-template-item", item.element).append(item.data.tmpl);
      } }).
    getKendoMobileCustomListView();

    self.configureHandlers();

  },

  configureHandlers: function configureHandlers() {
    var self = this;

    $.when(self.engineReady).then(function () {
      self.subscribe("dataNavigation", function (e, params) {
        self.entityDataSource.filter({ field: "data", value: params.data });
        self.entityList.scroller().reset();

        //Empty the array
        self.acumContext.splice(0, self.acumContext.length);

        self.acumContext.push(params.data);
        self.navbar.title(params.data.displayName);
        self.mashupFacade.navigate("#entityTemplates", "slide");
      });

      self.engine.subscribe("onLoadDataNavigation", function (ev, params) {
        self.entityDataSource.filter({ field: "data", value: params.data });
        self.entityList.scroller().reset();

        self.acumContext.push(params.data);
        self.navbar.title(params.data.displayName);
      });
    });

    $(".bz-render-back", self.view.header).bind("click", function () {
      self.acumContext.splice(self.acumContext.length - 1, 1);
      if (self.acumContext.length > 0) {
        var data = self.acumContext.slice(self.acumContext.length - 1, 1)[0];
        self.entityDataSource.filter({ field: "data", value: data });
        self.entityList.scroller().reset();

        self.navbar.title(data.displayName);
      } else
      {
        self.mashupFacade.navigate("#render", "slide:right");
      }
    });
  },

  getEntities: function getEntities(params) {
    var self = this;
    return self.getData(params).then(function (entityData) {
      return self.getEntityTemplates(entityData);
    });
  },

  getData: function getData(params) {
    var self = this;
    return self.dataService.getCollectionEntityData(params).then(function (data) {
      return data;
    });
  },

  /**
      * Invokes the template engine, to gets the custom columns
      * @param entityData
      * @return {Deferred}
      */
  getEntityTemplates: function getEntityTemplates(entityData) {
    var self = this;
    var templateType = self._referenceType === "ENTITY" ? "Content" : "List";
    var processEntities = [];

    //Se crea una promesa por cada template
    var promises = $.map(entityData.entities, function (entity) {
      processEntities.push({ surrogateKey: entity.surrogateKey, entityId: entity.guid });
      return self.engine.render($.extend(entity, { isDefaultTemplate: entity.guidTemplate === null, templateType: templateType })).then(function (renderedTemplate) {
        entity.tmpl = renderedTemplate;
      });
    });

    return $.when.apply($, promises).then(function () {
      return entityData;
    });
  },

  dataCalled: function dataCalled(data) {
    var self = this;
    if (data.entities.length > 0) {
      self.view.content.find(".activity-empty-list").hide();
    } else {
      self.view.content.find(".activity-empty-list").show();
    }
  } });