/*
 *   Name: BizAgi mashup Main Controller
 *   Author: Ricardo Prez
 *   Comments:
 *   -   This script will define a base class to handle mashup layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */


// Defines the main controller
bizagi.mashup.controllers.caseSummary.extend("bizagi.mashup.controllers.caseSummary", {}, {

  init: function init(mashupFacade, dataService, params) {
    var self = this;
    //Load templates
    self._super(mashupFacade, dataService, params);

    self.loadTemplates({
      "mashup-summary-view": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-view"),
      "mashup-summary-main": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-main"),
      "mashup-summary-subprocess": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-subprocess"),
      "mashup-summary-comments": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-comments"),
      "mashup-summary-assigness": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-assigness"),
      "mashup-summary-activities": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-activities"),
      "mashup-summary-events": bizagi.getTemplate("bizagi.mashup.controller.tablet.caseSummary").concat("#mashup-summary-events"),
      useNewEngine: false });

  },

  postRender: function postRender() {
    var self = this;
    self._super();
    self.configureHandlers();
  },

  /*
      * Event Handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    self._super();

    self.subscribe("renderSummaryCase", function (e, params) {
      return self.renderSummary(params);
    });
  },

  /**
      * Renders the summary component of the controller
      * @param params
      * @returns {*}
      */
  renderSummary: function renderSummary(params) {
    var self = this;
    var defer = new $.Deferred();

    self.params = params || {};
    self.cache = {};

    self.dataService.summaryCaseDetails({
      idCase: params.idCase,
      eventAsTasks: params.eventAsTasks || false,
      onlyUserWorkItems: params.onlyUserWorkItems || false,
      idWorkitem: params.idWorkitem,
      isOfflineForm: params.isOfflineForm || false }).
    done(function (data) {
      var date = typeof data.estimatedSolutionDate !== "undefined" ? data.estimatedSolutionDate : data.solutionDate;
      data.params = self.params;

      data.group = bizagi.util.cases.getStatusTimeLabel(date);
      self.data = data;

      self.data.estimatedSolutionDate = bizagi.util.dateFormatter.formatDate(new Date(self.data.estimatedSolutionDate), self.getResource("dateFormat") + " " + self.getResource("timeFormat"));
      self.data.solutionDate = bizagi.util.dateFormatter.formatDate(new Date(self.data.solutionDate), self.getResource("dateFormat") + " " + self.getResource("timeFormat"));
      self.data.creationDate = bizagi.util.dateFormatter.formatDate(new Date(self.data.creationDate), self.getResource("dateFormat") + " " + self.getResource("timeFormat"));

      var mainTemplate = $.kendoTmpls(self.getTemplate("mashup-summary-main"), self.data);

      $(".km-scroll-container", self.content).empty().html(mainTemplate);
      if ($(".km-content", self.content).data("kendoMobileScroller")) {
        $(".km-content", self.content).data("kendoMobileScroller").reset();
      }

      /* istanbul ignore next */
      $(".ui-bizagi-container-tab", self.content).bztabs({
        activate: function activate(e) {
          bizagi.loading.start();

          data.currentTab = $(e.currentTarget).data("reference-tab");

          self.renderCurrentTab(data);
        },
        tabNumber: 2 });


      if (data.currentState && data.currentState.length > 0 && typeof data.currentState[0].allowReleaseActivity !== "undefined" && data.currentState[0].allowReleaseActivity) {
        /* istanbul ignore next */
        $(".bz-wp-summary-release", self.content).bind("click", function () {
          self.release(params);
        });
      }

      self.initialized = true;
      defer.resolve();
    });
    return defer.promise();
  } });