/*
 *   Name: BizAgi Tablet mashup Facade
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will define a workportal facade to access to all components
 */


$.Class.extend("bizagi.mashup.tablet.facade", {},
{
  /*
   *   Constructor
   */
  init: function init(mashup, dataService) {
    var self = this;
    this.mashup = mashup;
    this.templates = {};
    this.dataService = dataService;
    self.device = bizagi.detectDevice();

    // Add meta to head in order to disable zoom
    this.setMobileMetaTags();
    if (bizagi.override.enableMSF) {
      if (bizagi.hasOwnProperty("kendo")) {
        self.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
      } else {
        self.templateService = new bizagi.templates.services.service(bizagi.localization);
      }
    } else {
      self.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
    }
  },

  /* SETS Mobile META TAGS
      =====================================================*/
  setMobileMetaTags: function setMobileMetaTags() {

    // Removes iPad address bar when launched from home screen // temporarily disabled
    /*
     $('<meta>', {
     name : "apple-mobile-web-app-capable",
     content : "yes"
     }).appendTo('head');
     */
    // Disables safari zooming on iPad when running as web app
    $("<meta>", {
      name: "viewport",
      content: "width=device-width, initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no" }).
    appendTo("head");

    $("<link>", {
      rel: "apple-touch-icon-precomposed",
      href: "jquery/css/bizagi/tablet/images/BizAgi_logo.png" }).
    appendTo("head");

    /*$('<link>', {
                       rel: 'apple-touch-startup-image',
                       href: 'jquery/css/bizagi/tablet/images/splash-screen-landscape.png',
                       media: '(orientation:landscape)'
                       }).appendTo('head');
                         $('<link>', {
                       rel: 'apple-touch-startup-image',
                       href: 'jquery/css/bizagi/tablet/images/splash-screen-portrait.png',
                       media: '(orientation:portrait)'
                       }).appendTo('head');*/

  },

  /*
      *   This function will load asynchronous stuff needed in the module
      */
  initAsyncStuff: function initAsyncStuff(params) {
    params = params || {};
    var self = this;
    var device = params.device || bizagi.detectDevice();
    //TODO BUG IN ANDROID;
    //bizagi.enableCustomizations = true;

    if (bizagi.enableCustomizations) {
      return $.when(
      // Load custom templates
      bizagi.templateService.loadCustomTemplates().
      then(function () {
        // Load default templates
        return self.loadTemplates();
      }),
      // Load Custom styles
      bizagi.util.loadFile({
        src: bizagi.getStyleSheet("bizagi.overrides." + device + ".custom.styles"),
        type: "css" }),

      // Load custom javascript overrides
      bizagi.util.loadFile({
        src: bizagi.getJavaScript("bizagi.mashup." + device + ".overrides"),
        type: "js" }));


    } else {
      return self.loadTemplates();
    }
  },

  /*
      *   Load all the templates used in the workportal
      */
  loadTemplates: function loadTemplates() {
    var self = this;
    // var defer = new $.Deferred();

    return $.when(self.loadTemplate("home", bizagi.getTemplate("bizagi.mashup.common.home"))).
    then(function () {
      var def = new $.Deferred();

      // Resolve when all templates are loaded
      $("body").append(self.getTemplate("home"));

      bizagi.util.startkendo({
        started: function started() {
          self.configureHomeHandlers();
          def.resolve();
        } });


      return def.promise();
    });
  },

  configureHomeHandlers: function configureHomeHandlers() {
    var self = this;
    $(".bz-home-back", "#home").click(function (e) {
      self.close();
    });
  },

  navigate: function navigate(id, transition, replace) {
    if (typeof replace !== "undefined" && replace) {
      bizagi.kendoMobileApplication.replace(id, transition);
    } else
    {
      bizagi.kendoMobileApplication.navigate(id, transition);
    }
  },

  /**
      * Close the render view & call callback
      */
  close: function close() {
    if (typeof this.mashup.defaultParams.onClose !== "undefined") {
      this.mashup.defaultParams.onClose();
    }
    //this.navigate("#:back");
  },

  /*
      *   Load one template and save it internally
      */
  loadTemplate: function loadTemplate(template, templateDestination) {
    var self = this;
    // Go fetch the template
    return self.templateService.getTemplate(templateDestination, template).
    done(function (resolvedTemplate) {
      if (typeof resolvedTemplate === "string") {
        self.templates[template] = $.trim(resolvedTemplate.replace(/\n/g, ""));
      }
    });
  },

  /*
      *   Returns the desired action
      */
  getAction: function getAction(action, params) {
    if (action == "routing") {
      return new bizagi.mashup.actions.routing(this, this.dataService, params);
    }

    // No type supported
    bizagi.log(action + " action not supported", params, "error");
    return null;
  },

  /*
      *   Method to fetch templates from a private dictionary
      */
  getTemplate: function getTemplate(template) {
    var self = this;
    return self.templates[template];
  },

  /*
      *   Returns the main controller to be used
      */
  getMainController: function getMainController(params) {
    return new bizagi.mashup.controllers.main(this, this.dataService, params);
  },
  /*
     *   Load one template and save it internally
     */
  loadTemplateWebpart: function loadTemplateWebpart(template, templateDestination) {
    var self = this;

    // Go fetch the template
    return self.templateService.getTemplateWebpart(templateDestination, template).
    done(function (resolvedRemplate) {
      self.templates[template] = $.trim(resolvedRemplate.replace(/\n/g, ""));
    });
  } });