// Auto extend
$.Class.extend("bizagi.mashup.controllers.async", {
  ASYNC_CHECK_TIMER: 3000 },
{

  init: function init(renderController) {
    var self = this;
    self.renderController = renderController;
    self.configureHandlers();
  },

  configureHandlers: function configureHandlers() {
    var async = this;
    async.renderController.subscribe("render-async", function (e, params) {
      async.executeAsync(params).done(function () {
        async.renderController.publish("renderCase", {
          action: "routing",
          idCase: params.idCase });

      });
    });
  },

  executeAsync: function executeAsync(params) {
    var self = this;
    var defer = new $.Deferred();
    var $template = $.tmpl(self.renderController.getTemplate("mashup-async"), params);
    self.modal = new kendo.mobile.ui.ModalView($template,
    {
      close: function close() {
        this.destroy();
        this.element.remove();
      },
      modal: true });


    $(".render-async-close", self.modal.element).bind("click", function () {
      self.modal.close();
    });

    self.modal.open();

    self.checkAsycnExecutionState(defer, params);

    return defer.promise();
  },

  checkAsycnExecutionState: function checkAsycnExecutionState(defer, params) {
    var self = this.renderController;
    var async = this;
    $.when(self.dataService.getAsynchExecutionState({
      idCase: params.idCase })).
    done(function (response) {
      // verify errors in response
      if (response.state == "Error" && response.errorMessage !== undefined) {
        $("#modalview-async-message", self.modal.element).height(130);
        // Change default error
        $(".ui-bizagi-wp-async-error", self.modal.element).show();
        $(".ui-bizagi-wp-waiting-message", self.modal.element).hide();
        $(".ui-bizagi-wp-async-error-txt", self.modal.element).html(response.errorMessage || bizagi.localization.getResource("render-async-error"));
      }

      // Check for finish signal
      if (response.state == "Processing") {
        // Check for non-error message
        if (response.errorMessage.length === 0) {
          setTimeout(function () {
            // Refresh widget after a timer
            async.checkAsycnExecutionState(defer, params);
          }, bizagi.mashup.controllers.async.ASYNC_CHECK_TIMER);
        }

      } else if (response.state == "Finished") {
        // Resolve main deferred
        async.modal.close();
        defer.resolve();
      }
    });
  } });