/*
 *   Name: BizAgi Workportal Main Controller
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will define a base class to handle workportal layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */


// Defines the main controller
bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.main", {}, {

  /*
                                                                                     *   Constructor
                                                                                     */
  init: function init(mashupFacade, dataService, params) {
    var self = this;

    // Define regex to apply to templates
    self.componentRegex = /{{component (\w+)}}/g;

    // Call base
    this._super(mashupFacade, dataService, params);

    // Subscribe to change widget event
    this.subscribe("changeWidget", function (e, params) {

      $.when(bizagi.util.autoSave()).done(function () {

        $(document).data('auto-save', '');

        if (params.widgetName == bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX || params.widgetName == bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX_GRID) {
          bizagi.cookie("bizagiDefaultWidget", params.widgetName, { expires: 30 });
        }

        bizagi.util.setContext({
          widget: params.widgetName },
        true);

        self.setWidget(params);

      });

    });

    // Subscribe to pop widget event
    this.subscribe("showDialog", function (e, params) {
      // Shows a pop a widget
      switch (params.widgetName) {
        case "routing":
          self.renderController.renderRoutingDialog(params);
          break;
        default:
          break;}

    });


    // Subscribe to push widget event
    this.subscribe("pushWidget", function (e, params) {
      // Shows a push a widget
      self.pushWidget(params);
    });

    // Subscribe to execute action event
    this.subscribe("executeAction", function (e, params) {
      // Executes the action
      self.executeAction(params);
    });
    /**
         * subscribe the error
         */
    this.subscribe("renderActionButton", function (e, params) {
      // Executes the action
      if (typeof self.mashupFacade.mashup.defaultParams.onRenderAction !== "undefined") {
        self.mashupFacade.mashup.defaultParams.onRenderAction(params);
      }
    });


    this.subscribe("error", function (e, params) {
      // attach the widget to a given element
      if (typeof self.mashupFacade.mashup.defaultParams.onError !== "undefined") {
        var result = {};
        try {
          result = $.parseJSON(params.responseText);
        } catch (e) {
          try {
            // Try to fix some known problems regarding rare ascii characters
            result = $.parseJSON(data.replace(/\x1d/g, "."));
          } catch (ex) {
            result.type = "error";
            result.code = e.errorCode;
            result.message = params ? params.responseText : "";
            //Throw exception in next layer
          }
        }

        self.mashupFacade.mashup.defaultParams.onError(result);
      }
      //self.mashupFacade.navigate("#home");
    });

    this.subscribe("mashup-resize-content", function (e, params) {
      self.mashupFacade.mashup.defaultParams.onMashupResize(params);
    });

  },

  /*
      *   Renders the content for the current controller
      *
      */
  renderContent: function renderContent() {
    var self = this;
    // render workarea
    return self.renderWorkarea();
  },

  /*
      *   Renders the workarea based on the current widget
      */
  renderWorkarea: function renderWorkarea() {
    var self = this;
    // clean widgets
    //TODO Implementacion de un factory de controllers

    self.renderController = new bizagi.mashup.controllers.render(self.mashupFacade, self.dataService, { canvas: self.canvas, layoutHeader: self.creationParams.layoutHeader || false });
    self.caseSummaryController = new bizagi.mashup.controllers.caseSummary(self.mashupFacade, self.dataService, { canvas: self.canvas });
    self.startFormController = new bizagi.mashup.controllers.startForm(self.mashupFacade, self.dataService, { canvas: self.canvas });
    self.entityTemplatesController = new bizagi.mashup.controllers.entityTemplates(self.mashupFacade, self.dataService, { canvas: self.canvas, layoutHeader: self.creationParams.layoutHeader || false });

    //starts non visual controllers

    self.newCaseController = new bizagi.mashup.controllers.newCase(self.mashupFacade, self.dataService, { canvas: self.canvas, layoutHeader: self.creationParams.layoutHeader || false });

    return $.when(self.renderController.render(),
    self.caseSummaryController.render(),
    self.newCaseController.render(),
    self.startFormController.render(),
    self.entityTemplatesController.render());

  } });