/*
 *   Name: BizAgi mashup entityTemplates Controller
 *   Author: Ricardo Prez
 *   Comments:
 *   -   This script will define a base class to handle mashup layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */

bizagi.mashup.controllers.entityTemplates.extend("bizagi.mashup.controllers.entityTemplates", {
  pageSize: 30 },
{

  init: function init(mashupFacade, dataService, params) {
    var self = this;
    //Load templates
    self._super(mashupFacade, dataService, params);

    self.loadTemplates({
      "mashup-entity-templates": bizagi.getTemplate("bizagi.mashup.common.entityTemplates").concat("#mashup-entity-templates"),
      "mashup-entity-templates-item": bizagi.getTemplate("bizagi.mashup.common.entityTemplates").concat("#mashup-entity-templates-item"),
      "mashup-entity-templates-item-actions": bizagi.getTemplate("bizagi.mashup.common.entityTemplates").concat("#mashup-entity-templates-item-actions"),
      "mashup-entity-templates-pull-loader": bizagi.getTemplate("bizagi.mashup.common.entityTemplates").concat("#mashup-entity-templates-pull-loader"),
      "mashup-entity-templates-refresh-loader": bizagi.getTemplate("bizagi.mashup.common.entityTemplates").concat("#mashup-entity-templates-refresh-loader"),
      useNewEngine: false });


    var proxyPrefix = params.proxyPrefix || dataService.serviceLocator.proxyPrefix;
    var templateDataService = new bizagi.render.services.service({ proxyPrefix: proxyPrefix });
    var renderFactory = new bizagi.rendering.tablet.factory(templateDataService);

    //Sets a promise in order to know when the template engine is loaded
    self.engineReady = $.when(renderFactory.initAsyncStuff()).then(function () {
      self.engine = new bizagi.templateEngine({ renderFactory: renderFactory, cache: true, forcePersonalizedColumns: false });
    });
  } });