/*
 *   Name: BizAgi Workportal new Case Controller
 *   Author: RicardoPD on 2/16/2017.
 *   Comments:
 *      - this script will allow the bizagi case creation
 */

bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.newCase", {}, {


  /**
                                                                                        * Constructor
                                                                                        * @param mashupFacade
                                                                                        * @param dataService
                                                                                        * @param params
                                                                                        */
  init: function init(mashupFacade, dataService, params) {
    var self = this;
    self._super(mashupFacade, dataService, params);

    if (bizagi.hasOwnProperty("kendo")) {
      this.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
    } else {
      this.templateService = new bizagi.templates.services.service(bizagi.localization);
    }

    self.loadTemplates({
      "mashup-newcase-modal-multiorganization": bizagi.getTemplate("bizagi.mashup.common.newCase").concat("#mashup-newcase-multiorganization"),
      useNewEngine: false });

  },
  /**
      * This controller haven''t a visual interface
      */
  renderContent: function renderContent() {
  },

  postRender: function postRender() {
    this.configureHandlers();
  },

  /**
      * Configures all the events implementation
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    self.subscribe("createNewCase", function (e, params) {
      if (typeof bizagi.currentUser !== "undefined" && bizagi.util.parseBoolean(bizagi.currentUser.isMultiOrganization)) {
        return self.createOrganizationList(params);
      } else
      if (params.notification) {
        if (params.isBatch) {
          return self.createNewCaseNotification(params);
        } else {
          return self.hasStartForm(params);
        }
      }
      return self.createNewCase(params);
    });
  },

  /**
      * Creates a simple new case
      * @param newCaseParams
      * @returns {jQuery.Deferred}
      */
  createNewCase: function createNewCase(newCaseParams) {
    var self = this;
    var deferred = new $.Deferred();

    bizagi.loading.start();

    if (newCaseParams.isOfflineForm) {
      var dataCase = {
        idWfClass: newCaseParams.idProcess || newCaseParams.idWFClass,
        displayName: newCaseParams.displayName,
        isOfflineForm: newCaseParams.isOfflineForm,
        categoryName: newCaseParams.categoryName };


      return self.createCase(dataCase);
    } else {
      $.when(self.dataService.startProcess(newCaseParams)).then(function (resultData) {
        var response = self.buildCaseParameters(newCaseParams, resultData);

        if (response.hasStartForm) {
          response.displayName = newCaseParams.displayName;
          self.publish("renderStartForm", { params: response });
          self.mashupFacade.navigate("#startForm");
        } else {
          self.publish("renderCase", response);
          self.mashupFacade.navigate("#render");
        }

        deferred.resolve();
      }).fail(function (msg) {/* istanbul ignore next: untestable fail */
        var response = $.parseJSON(msg.responseText);
        bizagi.showMessageBox(response.message, "Error");
        deferred.fail();
      }).always(function () {
        bizagi.loading.stop();
      });

      return deferred;
    }
  },

  /**
      * Create new case for organizations
      * @param newCaseParams
      * @returns
      */
  createCase: function createCase(newCaseParams) {
    var self = this;
    bizagi.loading.start();

    return self.dataService.createNewCase(newCaseParams).then(function (response) {
      self.publish("renderCase", response);
      self.mashupFacade.navigate("#render");

    }).fail(function (msg) {
      var response = $.parseJSON(msg.responseText);
      bizagi.showMessageBox(response.message, "Error");

    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      *
      * @param newCaseParams
      * @returns {*}
      */
  hasStartForm: function hasStartForm(newCaseParams) {
    var self = this;
    newCaseParams = newCaseParams || {};

    if (!newCaseParams.hasStartForm && newCaseParams.isShortcut) {
      return self.startShortcutProcess(newCaseParams);
    } else {
      return self.createNewCaseNotification(newCaseParams);
    }
  },

  /**
      *
      * @param newCaseParams
      */
  startShortcutProcess: function startShortcutProcess(newCaseParams) {
    var self = this;
    self.dataService.actionCreateCase(newCaseParams).done(function (caseResponse) {
      var idCase = caseResponse.caseId;

      self.dataService.getCaseAssignees({ idCase: idCase }).done(function (assignessResponse) {
        var assignees = assignessResponse.assignees;
        var navigateProcess = false;

        if (typeof assignees !== "undefined" && assignees.length > 0) {
          var currentUser = bizagi.currentUser.idUser;
          var assigned = assignees.find(function (el) {
            return el.idUser == currentUser;
          });

          if (typeof assigned !== "undefined") {
            navigateProcess = true;
          }
        }

        if (navigateProcess) {
          self.publish("renderCase", { action: "routing", idCase: idCase });
        } else {
          var message = bizagi.localization.
          getResource("workportal-widget-templateengine-action-process-notification");
          message = message.replace("{0}", idCase);

          $.notifier.add({ text: message });
        }
      });
    }).fail(function (msg) {
      var response = $.parseJSON(msg.responseText);
      bizagi.showMessageBox(response.message, "Error");
    });
  },

  /**
      * Create list of organizations
      * @param newCaseParams
      * @returns
      */
  createOrganizationList: function createOrganizationList(newCaseParams) {
    var self = this;
    var deferred = new $.Deferred();

    // Initialize adjusted modalview
    self.isAdjusted = false;

    self.dataService.getOrganizationsList().
    done(function (data) {

      var template = self.getTemplate("mashup-newcase-modal-multiorganization");
      var multiorganizationTemplate = kendo.template(template, { useWithBlock: false });

      var multiorganizationModal = multiorganizationTemplate({
        list: data.response,
        idWfClass: newCaseParams.idProcess,
        isAdhocProcess: newCaseParams.isAdhocProcess || false });


      var modalView = $(multiorganizationModal.trim()).kendoMobileModalView({
        close: function close() {
          this.destroy();
          this.element.remove();
        },
        useNativeScrolling: true,
        modal: false }).
      getKendoMobileModalView();

      // Configure Handler modal new Case
      self.configureModalHandlers();

      $(".bz-wp-modal-organization-footer div.km-rightitem").addClass("inactive");

      modalView.open();

      /* istanbul ignore next: untestable */
      modalView.adjustSize = function () {
        // Adjust summary popup to window size
        var maxHeight;
        var listHeight = $(".bz-wp-modal-organization-list", this.element).height();

        // Do nothing
        if (listHeight === 0) return;

        var maxWidth = $(".bz-wp-modal-organization-content", this.element).width();
        var popupHeight = $(".bz-wp-modal-organization-header", this.wrapper).height() +
        $(".bz-wp-modal-organization-footer", this.wrapper).height();
        var windowHeightSize = window.innerHeight;
        var windowWidthSize = window.innerWidth;

        if (listHeight + popupHeight >= windowHeightSize) {
          maxHeight = windowHeightSize - 20;
        } else {
          maxHeight = listHeight + popupHeight;

          if (bizagi.util.detectDevice() !== "tablet_ios") {
            maxHeight += !self.isAdjusted ? 20 : Math.abs(window.orientation) === 90 ? 8 : 35;
          } else {
            maxHeight += Math.abs(window.orientation) === 90 ? 20 : 22;
          }
        }

        var top = (windowHeightSize - maxHeight) / 2;
        var left = (windowWidthSize - maxWidth) / 2;

        this.wrapper.parent().css("top", top);
        this.wrapper.parent().css("left", left);
        this.wrapper.parent().css("height", maxHeight);

        self.isAdjusted = true;
      };

      modalView.adjustSize();

      /* istanbul ignore next: untestable */
      window.addEventListener("orientationchange", function () {
        if (bizagi.util.isAndroidDevice()) {
          setTimeout(function () {
            modalView.adjustSize();
          }, 500);
        } else {
          modalView.adjustSize();
        }
      }, false);

      deferred.resolve();
    }).fail(function (msg) {
      var message;
      var responseText = msg.responseText;

      try {
        message = JSON.parse(responseText);
        message = message.message || responseText;
      } catch (e) {
        message = responseText;
      }

      bizagi.showMessageBox(message, "Error");
      deferred.fail();
    }).always(function () {
      bizagi.loading.stop();
    });

    return deferred;
  },

  /**
      *
      * @param newCaseParams
      * @returns {jQuery.Deferred}
      */
  createNewCaseNotification: function createNewCaseNotification(newCaseParams) {
    var self = this;
    var deferred = new $.Deferred();

    if (newCaseParams.hasStartForm) {
      self.publish("renderStartForm", { params: newCaseParams });
      self.mashupFacade.navigate("#startForm");

      bizagi.loading.stop();

      deferred.resolve();
    } else {
      self.dataService.actionCreateCase(newCaseParams).
      done(function (resp) {
        var mapping = newCaseParams.mapping || {};
        var type = newCaseParams.type || "Process";

        var response = {
          showProcess: false,
          isMultiInstance: newCaseParams.isMultiInstance,
          notification: true,
          hasStartForm: resp.hasStartForm,
          idProcess: resp.idProcess,
          displayName: newCaseParams.displayName,
          mapping: mapping,
          type: type,
          idParentCase: newCaseParams.idParentCase };


        $.extend(response, resp.caseInfo);

        if (newCaseParams.isBatch) {
          var message = bizagi.localization.
          getResource("workportal-widget-templateengine-action-process-notification");
          message = message.replace("{0}", resp.caseNumber);

          var title = printf(bizagi.localization.
          getResource("workportal-widget-templateengine-message-summary-create-cases-singular"), 1);
          $.notifier.add({ title: title, text: message, sticky: true });


          bizagi.loading.stop();
        } else {/* istanbul ignore next: untestable fail */
          self.dataService.getCaseAssignees({ idCase: resp.caseId }).
          done(function (response) {
            var assignees = response.assignees;
            var navigateProcess = false;

            if (typeof assignees !== "undefined" && assignees.length > 0) {
              var currentUser = bizagi.currentUser.idUser;
              var assigned = assignees.find(function (el) {
                return el.idUser == currentUser;
              });

              if (typeof assigned !== "undefined") {
                navigateProcess = true;
              }
            }

            bizagi.loading.stop();

            if (navigateProcess) {
              self.publish("render-case", { action: "routing", idCase: resp.caseId });
            } else {
              var message = bizagi.localization.
              getResource("workportal-widget-templateengine-action-process-notification");
              message = message.replace("{0}", resp.caseNumber);
              var title = printf(bizagi.localization.
              getResource("workportal-widget-templateengine-message-summary-create-cases-singular"), 1);
              $.notifier.add({ title: title, text: message, sticky: true });
            }
          });
        }

        deferred.resolve();
      }).fail(function (msg) {/* istanbul ignore next: untestable fail */
        var response = $.parseJSON(msg.responseText);
        bizagi.loading.stop();

        $.notifier.add({
          title: "Error",
          class_name: "error",
          text: response.message,
          sticky: true });


        deferred.fail();
      });
    }
    return deferred;
  },

  /**
      * Configure events handler
      * @returns {}
      */
  configureModalHandlers: function configureModalHandlers() {
    var self = this;

    /* Modal Summary*/
    // .bz-wp-modal-organization-close
    $(".bz-wp-modal-organization").delegate(".bz-wp-modal-organization-footer .km-leftitem", "click",
    function () {
      $(".bz-wp-modal-organization").data("kendoMobileModalView").close();
    });

    $(".bz-wp-modal-organization").delegate(".bz-wp-modal-organization-list > li", "click", function (event) {
      var item = $(event.target);

      // Remove others selected
      $(".bz-wp-modal-organization-list > li").removeClass("bz-wp-modal-organization-list-item-selected");

      // Enable Create new case
      if ($(".bz-wp-modal-organization-footer div.km-rightitem").hasClass("inactive")) {
        $(".bz-wp-modal-organization-footer div.km-rightitem").removeClass("inactive");
        $(".bz-wp-modal-organization-footer div.km-rightitem").addClass("active");
      }

      // Selected item
      item.addClass("bz-wp-modal-organization-list-item-selected");
    });

    // .bz-wp-modal-organization-newcase
    $(".bz-wp-modal-organization").delegate(".bz-wp-modal-organization-footer .km-rightitem", "click",
    function (event) {
      event.stopPropagation();

      // If not selected item not action
      if ($(".bz-wp-modal-organization-footer div.km-rightitem").hasClass("inactive")) return;

      bizagi.loading.start();

      var modalContext = $(this).closest(".bz-wp-modal-organization");
      var isAdhocProcess = modalContext.data("adhoc");
      var caseNumber = modalContext.data("workflow");
      var idOrganization = $(".bz-wp-modal-organization-list-item-selected").data("id") || null;

      var newCaseParams = {};
      if (isAdhocProcess) {
        newCaseParams.idProcess = caseNumber;
        return self.createNewCase(newCaseParams);
      } else {
        newCaseParams.idWfClass = caseNumber;
        newCaseParams.idOrganization = idOrganization;
        return self.createCase(newCaseParams);
      }
    });
  },

  buildCaseParameters: function buildCaseParameters(newCaseParams, data) {
    var response;

    if (data.oldVersion) {
      response = data;
      response["hasStartForm"] = (newCaseParams.isOfflineForm ? false : data.hasStartForm) || false;
      response["displayName"] = newCaseParams.displayName;
    } else {
      var mapping = newCaseParams.mapping || {};
      var type = newCaseParams.type || "Process";

      response = {
        showProcess: true,
        isMultiInstance: newCaseParams.isMultiInstance,
        hasStartForm: newCaseParams.isOfflineForm ? false : data.hasStartForm,
        idProcess: data.idProcess,
        displayName: newCaseParams.displayName,
        mapping: mapping,
        type: type,
        idParentCase: newCaseParams.idParentCase };


      $.extend(response, data.caseInfo);
    }

    return response;
  } });