/*
 *   Name: BizAgi Workportal Main Controller
 *   Author: RicardoPD on 2/16/2017.
 *   Comments:
 *      - this script will allow the bizagi case creation with start form
 */

bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.startForm", {}, {

  /**
                                                                                          * Constructor
                                                                                          * @param mashupFacade
                                                                                          * @param dataService
                                                                                          * @param params
                                                                                          */
  init: function init(mashupFacade, dataService, params) {
    var self = this;
    self._super(mashupFacade, dataService, params);
  },
  /**
      * This controller haven''t a visual interface
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("mashup-start-form");
    // Render content
    return $.kendoTmpls(template, {});
  },

  postRender: function postRender() {
    var self = this;
    self.view = new kendo.mobile.ui.View(self.content, { "useNativeScrolling": true }); //self.initNavigation();
    self.view.header.hide();
    this.configureHandlers();
  },

  configureHandlers: function configureHandlers() {
    var self = this;

    self.subscribe("renderStartForm", function (e, params) {
      return self.renderForm(params.params);
    });

    $(".bz-render-back", self.view.header).bind("click", function () {
      self.view.contentElement().empty();
      self.mashupFacade.close();
    });

    // Attach handler to render container to subscribe for routing events
    self.view.content.bind("routing", function (context, triggerparams) {
      if (triggerparams.notification) {
        var message = bizagi.localization.getResource("workportal-widget-templateengine-action-process-notification");
        message = message.replace("{0}", triggerparams.idCase);
        $.notifier.add({ text: message });
        self._back();
      } else if (typeof triggerparams.idCase !== "undefined") {
        // Executes routing action
        var params = {
          action: "routing", //bizagi.workportal.actions.action.BIZAGI_WORKPORTAL_ACTION_ROUTING
          idCase: triggerparams.idCase };


        self.dataService.getCaseAssignees(triggerparams).done(function (response) {
          var assignees = response.assignees;
          self._navigateProcess = false;

          if (typeof assignees !== "undefined" && assignees.length > 0) {
            var currentUser = bizagi.currentUser.idUser;
            var assigned = assignees.find(function (el) {
              return el.idUser == currentUser;
            });

            if (typeof assigned !== "undefined") {
              self._navigateProcess = true;
            }
          }

          if (self._navigateProcess) {
            bizagi.loading.start();
            $.when(self.publish("renderCase", params)).done(function () {
              bizagi.loading.stop();
              self.mashupFacade.navigate("#render");
            });

          } else {
            var message = bizagi.localization.getResource("workportal-widget-templateengine-action-process-notification");
            message = message.replace("{0}", params.idCase);

            self._refreshTemplate();

            //if(self.original_params.showConfirmation) {
            $.notifier.add({ text: message, sticky: true });
            //}
            bizagi.loading.stop();
          }
        });
      }
    });
  },

  /**
      * Renders the form component of the widget
      * @param params
      * @return {*}
      */
  renderForm: function renderForm(params) {
    var self = this;
    self.view.content.empty();

    var navbar = $(".km-navbar", self.view.header).data("kendoMobileNavBar");
    navbar.title(params.displayName);

    self.original_params = params;

    self._type = params.type;
    self._navigateProcess = typeof params.showProcess !== "undefined" ? params.showProcess : false;
    self._origin = params.origin;

    if (params.type === "Process") {
      self._renderParams = {
        h_action: params.hasStartForm ? "LOADSTARTFORM" : "LOADFORM" };

      if (typeof params.idParentCase !== "undefined") {
        self._renderParams.h_idParentCase = params.idParentCase;
      }
      if (typeof params.guidProcess !== "undefined") {
        self._renderParams.h_guidprocess = params.guidProcess;
        if (typeof params.mapping === "undefined" && params.isShortcut) {
          self._renderParams.h_mappingstakeholders = true;
        }
      } else {
        self._renderParams.h_idProcess = params.idProcess;
      }
      if (params.mapping) {
        for (var i = 0; i < params.mapping.length; i++) {
          var map = params.mapping[i];
          if (map.surrogateKey) {
            self._renderParams[map.xpath] = JSON.stringify(map.surrogateKey);
          }
        }
      }
    } else if (params.type === "Form") {
      self._renderParams = {
        h_action: "LOADENTITYFORM",
        h_contexttype: "entity",
        h_guidEntity: params.entityGuid,
        h_guidForm: params.formGuid,
        h_showDisabled: false,
        h_readonlyform: params.readonlyform };

      if (params.entitySurrogateKey !== null) {
        self._renderParams.h_surrogateKey = params.entitySurrogateKey;
      }
      if (typeof params.mapping !== "undefined" && params.mapping !== null) {
        self._renderParams.h_mapping = JSON.stringify(params.mapping);
      }
    }

    // Load render page
    /* istanbul ignore if: rendering is declared on unit test */
    if (typeof self.rendering === "undefined") {
      var proxyPrefix = params.proxyPrefix || self.dataService.serviceLocator.proxyPrefix;
      bizagi.rendering.facade.extend("bizagi.rendering.startFormFacade", {}, {
        process: function process(params) {
          return this._super(params).then(function (form) {
            self._form = form;
            self.changeButtons(params.origin);
            return form;
          });
        } });

      self.navigation = new bizagi.mashup.controller.navigation(self);
      self.rendering = new bizagi.rendering.startFormFacade({
        "proxyPrefix": proxyPrefix,
        "device": bizagi.util.detectRealDevice() });

    }

    bizagi.util.setContext({
      idCase: params.idCase || null,
      displayName: params.displayName || params.taskName,
      caseNumber: params.caseNumber || null });



    return $.when(self.dataService.getForm(self._renderParams)).then(function (data) {
      return self.rendering.execute({
        data: data,
        navigation: self.navigation,
        originalParams: params,
        postRenderEdit: self.postRenderEdit.bind(self),
        getTemplate: self.getTemplate,
        canvas: self.view.content,
        type: "" });

    }).done(function () {
      self.view.scroller.reset();
      self.resizeParent($("#startForm .bz-form"));
    });
  },

  /**
      * edit Items on render
      */
  postRenderEdit: function postRenderEdit() {
  },

  /**
      *
      * @private
      */
  changeButtons: function changeButtons() {
    var self = this;
    var form = self._form;
    var originalProcessButton = form.processButton;

    if (form) {
      form.processButton = function (buttonProperties) {
        if (buttonProperties.action === "cancel") {
          form.endLoading();
          self._back();
        } else {
          bizagi.loading.start();

          if (typeof self._renderParams.h_surrogateKey !== "undefined") {
            form.properties.surrogateKey = self._renderParams.h_surrogateKey;
          }
          form.properties.contextType = self._renderParams.h_contexttype;

          if (form.validateForm()) {
            $.forceCollectData = true;
            $.when(originalProcessButton.apply(form, arguments)).then(function () {

              if (typeof self._renderParams.h_surrogateKey !== "undefined" || typeof self._renderParams.h_mapping !== "undefined") {
                self.refreshTemplate();
              }

            }).always(function () {
              bizagi.loading.stop();
              $.forceCollectData = false;
            });
          } else
          {
            bizagi.loading.stop();
          }
        }
      };
      if (self._type === "Form") {
        form.bind("refresh", function () {
          bizagi.loading.stop();
        });
      }
    }
  },

  /**
      *
      * @private
      */
  refreshTemplate: function refreshTemplate() {
    var self = this;

    if (typeof self._origin !== "undefined") {
      var accumulatedContext = self.homePortalFramework.accumulatedContext;
      var args = {
        data: accumulatedContext[accumulatedContext.length - 1],
        filters: [],
        eventType: "DATA-NAVIGATION",
        calculateFilters: true };


      self._back();

      self.publish("homeportalShow", { "what": self._origin, "title": typeof args.data !== "undefined" ? args.data.displayName : "", "params": args, "preventNavigate": true });
      //TODO dejar unicamente el homeportalShow con el refresh cuando se soporte la actualizacin de un solo resgitro
      //self.publish("homeportalShow", {"what": origin, params : {refresh:true, surrogateKey: self._renderParams.h_surrogateKey, idCase: bizagi.context.idCase}});
    } else {
      self._back();
    }
  } });